import * as React from 'react';
import { environment, Controller } from '@ali/4ever-cangjie';
import { CommonLine } from '@ali/we-design';
import updateHr from '../commands/updateHr';
import HrControl from './hrControl';
import {
  InlineToolbar,
} from '../../../index';

interface IProps {
  isSelected?: boolean;
  payload?: any;
  attributes?: any;
}

const hrLayout = [{
  toolButtons: [{
    name: 'hr.color',
  }, {
    name: 'hr.style',
  }, {
    name: 'hr.weight',
  }],
}];

const Hr: React.FC<IProps> = props => {
  const { isSelected, payload, attributes } = props;
  const controller = Controller.useController();
  const disableInlineToolbar = controller.query('hasHoverToolbar', 'hr');
  return (
    <InlineToolbar
      visible={!disableInlineToolbar && !environment.IS_MOBILE && isSelected}
      inlineLayout={hrLayout}
    >
      <HrControl attributes={attributes} onHrChange={updateHr} isSelected={isSelected} {...props}>
        <CommonLine {...payload} />
      </HrControl>
    </InlineToolbar>
  );
};

export default Hr;
